<template>
  <div class="manager-container">
    <el-dialog :visible="dialogVisible" title="添加账户" :before-close="close" width="600px">
      <el-form label-width="60px">
        <el-form-item label="用户名">
          <el-input v-model="form.uName" class="width" />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.pwd" class="width" />
        </el-form-item>
        <el-form-item label="角色">
          <el-select v-model="form.id">
            <el-option v-for="(item,index) of roleList" :key="index" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="add"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible="modifyStatus" title="修改账户" :before-close="close" width="600px">
      <el-form label-width="60px">
        <el-form-item label="用户名">
          <el-input v-model="form.uName" class="width" />
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.pwd" class="width" />
        </el-form-item>
        <el-form-item label="角色">
          <el-select v-model="form.id">
            <el-option v-for="(item,index) of roleList" :key="index" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="confirmModify"
        >确 定</el-button>
      </span>
    </el-dialog>
    <div class="add" @click="dialogVisible=true">
      添加账号
    </div>
    <div class="sort-content">
      <el-table
        :data="managerList.list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column prop="id" label="序号" />
        <el-table-column prop="user_name" label="角色账户名称" />
        <el-table-column label="角色" prop="role_id" />
        <el-table-column label="状态" prop="desStatus" />
        <el-table-column prop="created_at" label="创建时间" :show-overflow-tooltip="true" />
        <el-table-column width="250px">
          <template slot="header" :slot-scope="scope">
            操作
          </template>
          <template slot-scope="scope">
            <el-button size="mini" @click="modify(scope.row.id)">
              修改
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="onChange(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="managerList.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            scope: "scope",
            pages: 1,
            limit: 9,
            dialogVisible:false,
            modifyStatus:false,
            managerList: {},
            roleList:[],
            form:{

            },
        };
    },
    created() {
        this.getManagerList();
        this.getRoleList();
    },
    methods: {
    // 获取管理员列表
        async getManagerList() {
            try {
                let res = await this.axios.get("/admin/getAdmin", {
                    params: {
                        pages: this.pages,
                        limit: this.limit,
                    },
                });
                this.managerList = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "启用" : "禁用",
                        });
                    }),
                });
            } catch (error) {
                console.log(error);
            }
        },
        // 获取角色列表
        async getRoleList (){
            try {
                let res = await this.axios.get("/admin/getRoleList");
                this.roleList = res.data.list;
                
            } catch (error) {
                console.log(error);
            }
        },
        async onChange (id){
            try {
                await this.axios.post("/admin/updateaccountSatte",{
                    "id":id
                });
                this.getManagerList();
            } catch (error) {
                console.log(error);
            }

        },
        // 上一页
        prev() {
            this.pages--;
            this.getManagerList();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this.getManagerList();
        },
        // 下一页
        next() {
            this.pages++;
            this.getManagerList();
        },
        async add (){
            try {
                let res = await this.axios.post("/admin/addaccount",{
                    "user_name":this.form.uName,
                    "password":this.form.pwd,
                    "role_id":this.form.id
                });
                this.close();
                this.$message.success({
                    message:res.msg
                });
                this.pages  = 1;
                this.getManagerList();
                
            } catch (error) {
                console.log(error);
            }

        },
        modify(id){
            let current = this.managerList.list.find(item => item.id === id);
            this.form.userId = current.id;
            this.modifyStatus = true;

        }, 
        async confirmModify(){
            try {
                let res = await this.axios.post("/admin/updateaccount",{
                    "user_name":this.form.uName,
                    "user_id":this.form.userId,
                    "role_id":this.form.id,
                    "password":this.form.pwd
                });
                this.$message.success({
                    message:res.msg
                });
                this.close();
                this.getManagerList();
                 
            } catch (error) {
                console.log(error);
            }


        },
        close (){
            this.form = {};
            this.dialogVisible = false;
            this.modifyStatus = false;
        }
    },
    components: {},
};
</script>

<style lang="scss" scoped>
.manager-container {
  display: flex;
  flex-direction: column;
  .width{
      width: 300px;
  }
  .add {
    align-self: flex-end;
    border: 1px solid #f7961c;
    padding: 7px 20px;
    border-radius: 4px;
    margin: 15px 0;
    cursor: pointer;
  }
  .sort-content {
    height: 700px;
    .pagination {
      margin-top: 15px;
    }
  }
}
</style>
